<script>
import { ref,onMounted } from 'vue'
import Header from "../../components/global/header/HeaderArea2.vue"
import Footer from "../../components/global/footer/FooterArea.vue"
import { useRouter } from "vue-router"
import { Toast } from '../../components/global/swal'
import img_01 from '../../assets/icon/member01.svg'
import img_02 from '../../assets/icon/member02.svg'
import img_04 from '../../assets/icon/member04.svg'
import img_06 from '../../assets/icon/member06.svg'
import img_07 from '../../assets/icon/member07.svg'
import img_08 from '../../assets/icon/member08.svg'
import img_09 from '../../assets/icon/member09.svg'
import { apiGetCaretaker,apiDeleteCaretaker } from '@/api/api'
import { useUserStore } from '@/store/global'

export default {
  setup() {
    const userStore = useUserStore()
    const router = useRouter()
    const data = ref({})
    onMounted(async () => {
      if (userStore.userName == undefined) {
        // console.log('沒有')
        // call api
        data.value = await apiGetCaretaker()
        data.value = data.value.data
        userStore.userName = data.value
      } else {
        // console.log('有')
        data.value = userStore.userName
      }
    })

    const name = ['會員資料', '變更密碼', '履歷管理', '工單管理', '帳戶紀錄']
    const srcimg = [img_01, img_02, img_04, img_06, img_07]
    const link = ['/profile', '/password', '/resume/list', '/order/list', '/orderHistory/list']

    const goto = (val) => {
      router.push({ path: val })
    }
    const deleteCaretaker = async () => {
      await apiDeleteCaretaker()
      Toast.fire("帳號已刪除")
      router.push({ path: '/home' })
    }
    const logout =() =>{
      localStorage.clear()
      userStore.userName = undefined
      router.push({ path: '/' })
    }
    return { data, name, link, srcimg, goto, img_08,img_09, deleteCaretaker,logout };
  },
  components: {
    Header, Footer
  }
}
</script>

<template>
  <Header left="chevronArrow" path="/home" />
  <div class="member">
    <div class="main">
      <div class="mainblur">
        <div class="mb-3 topArea">
          <label class="headShot">
            <img :src="data.avatarUrl" v-if="data.avatarUrl" onerror="this.onerror=null; this.src='https://fakeimg.pl/106x106/'">
            <img src="@/assets/image/noavatar.jpg" v-if="!data.avatarUrl">
            <p>{{ data.name }}</p>
          </label>
        </div>
      </div>
    </div>
    <div class="main2">
      <ul class="list-group list-group-flush">
        <div class="listItem" v-for="i in name.length" :key="i.id">
          <button type="button" class="list-group-item list-group-item-action" @click="goto(link[i - 1])">
            <div><img :src="srcimg[i - 1]" />
              {{ name[i - 1] }}</div>
          </button>
        </div>
        <div class="listItem">
          <button type="button" class="list-group-item list-group-item-action" data-bs-toggle="modal"
            data-bs-target="#openCourse">
            <div><img :src=img_08 />停用帳號</div>
          </button>
        </div>
        <div class="listItem">
          <button type="button" class="list-group-item list-group-item-action" @click="logout">
            <div><img :src=img_09 />登出</div>
          </button>
        </div>
      </ul>
    </div>
    <!-- 確認刪除 -->
    <div class="modal fade" id="openCourse" tabindex="-1" aria-labelledby="openModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-body">
            <div class="form-container form-container-3">
              <div class="title-container">
                <div class="title">是否確認刪除帳號</div>
                <p class="text">帳號刪除後，您將無法再使用該帳號登入，是否確定要繼續?</p>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <div class="btn-container">
              <div class="row">
                <div class="col">
                  <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal">取消</button>
                </div>
                <div class="col">
                  <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal"
                    @click="deleteEmployer">確定刪除</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Footer />
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.member {
  margin-bottom: calc($footer-height + 15px);
  display: flex;
  flex-direction: column;

  .main {
    background-image: url('@/assets/image/member_bg.png');
    background-repeat: no-repeat;
    background-size: cover;
  }

  .mainblur {
    backdrop-filter: blur(3px);
    height: 12.875rem;
  }

  .topArea {
    // position: relative;
    // text-align: center;
    // line-height: 1;


    .headShot {
      margin-top: 1rem; 
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100vw;
      height: 12.875rem;

      img {
        width: 6.625rem;
        height: 6.625rem;
        border-radius: 50%;
        object-fit: cover;
        object-position: center top;
      }

      p {
        margin: 0.75rem auto;
        font-size: 1.25rem;
        font-weight: 700;
      }
    }
  }

  .listItem {
    border-bottom: 1px solid #E8E8E8;

    button {
      height: 50px;
      font-size: 1rem;
      font-weight: 400;
      border: none;
      color: #333333;
      display: flex;
      justify-content: space-between;
      align-items: center;

      img {
        width: 2rem;
        margin-right: 0.75rem;
      }
    }
  }
}
</style>